<template>
<div>
    <ul :class="nav_class">
          <li><router-link to="/index" class="item">首页</router-link></li>
          <li><router-link to="/about" class="item">关于</router-link></li>
          <li><router-link to="/tag" class="item">标签</router-link></li>
          <li><router-link to="/publish" class="item">发布</router-link></li>
          <li><a @click="dialogFormVisible = true" class="item">登录</a></li>
    </ul>
    <el-dialog
        class="formdialog"
        title="登录"
        width="20%"
        :visible.sync="dialogFormVisible">
        <el-form label-position="left" label-width="80px" :model="formlogin">
            <el-form-item label="用户名">
                <el-input v-model="formlogin.userName"></el-input>
            </el-form-item>
            <el-form-item label="密码">
                <el-input v-model="formlogin.password"></el-input>
            </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
            <el-button @click="dialogFormVisible = false">取 消</el-button>
            <el-button type="primary" @click="dialogFormVisible = false">确 定</el-button>
        </div>
    </el-dialog>
</div>
</template>

<script>
export default {
    name:"navmenu",
    data: function() {
    return {
      nav_class:"nav",
      start:0,
      dialogFormVisible:false,
      formlogin:{
          userName:'',
          password:''
      }
    }
  },
  mounted() {
    window.addEventListener("scroll",this.scrollevent);
  },
  methods: {
    scrollevent() {
      var height = window.pageYOffset || document.documentElement.scrollTop || document.body.scrollTop
      var distanse = document.documentElement.scrollTop
      if (height>49){
        this.nav_class = "nav nav-fixed nav-invisible"
      }
      else{
        this.nav_class = "nav"
      }
      if(distanse-this.start<0 && height!=0){
        this.nav_class = "nav nav-fixed"
      }
      this.start = distanse
    }
  }
}
</script>

<style>
.nav {
  position: fixed;
  width: 100%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: end;
  -ms-flex-pack: end;
  justify-content: flex-end;
  background-color: transparent;
  transition: all .4s;
  list-style: none;
  z-index: 100;
}
.nav .item {
  text-decoration: none;
  font-size: 1.2rem;
  display: block;
  margin: 0;
  padding: 1rem 1.2rem;
  opacity: 1;
  transition: opacity .4s;
  cursor: pointer;
  color: #fff;
}
.nav-fixed {
    background-color: hsla(0,0%,100%,.7);
    border-bottom: 1px solid #bababa;
    z-index: 1;
}
.nav-fixed .item {
  color: rgba(0,0,0,.8);
}
.nav-invisible {
    -webkit-transform: translateY(-4rem);
    transform: translateY(-4rem);
}
.el-dialog__body{
    margin-left: -20px;
    margin: 20px 0;
    border: 1px #d8d8d8 solid;
    padding: 0 60px;
}
</style>
